import "core-js/modules/es.array.push.js";
import myEventBus from '@/assets/js/bus';
import { useStore } from 'vuex';
import { reactive, toRefs, computed } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'HomeNav',
  setup() {
    const router = useRouter();
    const emitter = myEventBus();
    const store = useStore();
    const data = reactive({
      navleft: [{
        id: 'nav01',
        name: '推荐'
      }, {
        id: 'nav02',
        name: '附近'
      }, {
        id: 'nav03',
        name: '最新'
      }],
      num: 0,
      navright: [{
        id: 'nav05',
        name: '筛选'
      }, {
        id: 'nav06',
        name: '关键词'
      }],
      showgengxin: true,
      isactiveli: false
    });
    const sorestate = computed(() => store.state.city);
    const navleftclick = index => {
      if (data.num === index) {
        return false;
      }
      data.num = index;
      switch (data.navleft[index].id) {
        case 'nav01':
          emitter.emit('checknums', true);
          break;
        case 'nav02':
          emitter.emit('checknums', true);
          break;
        case 'nav03':
          emitter.emit('checknums', true);
          break;
      }
    };
    const navrightclick = id => {
      switch (id) {
        case 'nav05':
          router.push('/shaixuan');
          break;
        case 'nav06':
          router.push('/guanjianci');
          break;
      }
    };
    const citychoiceclick = () => {
      router.push('/citychoice');
      data.isactiveli = !data.isactiveli;
    };
    return {
      ...toRefs(data),
      sorestate,
      navleftclick,
      navrightclick,
      citychoiceclick
    };
  }
};